import React, { useState, useRef } from "react";
import s from "./CreateNews.module.css";
import { connect } from "react-redux";
import Input from "../../../../misc/Inputs/Input/Input";
import Button from "../../../../misc/Button/Button";
import FixedWrapper from "../../../../wrappers/FixedWrapper/FixedWrapper";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { Formik } from "formik";

const CreateNews = (props) => {
  const uploadInputRef = useRef();
  const handleImages = ({ target: { files } }, values, setValues) => {
    const reader = new FileReader();

    reader.onload = ({ target: { result } }) => {
      setValues({ ...values, gallery: result });
    };
    reader.readAsDataURL(files[0]);
  };

  return (
    <div>
      <div className={s.container}>
        <div className={s.title__container}>
          <h1 className={s.title}>Створення новини</h1>
        </div>
        <FixedWrapper>
          <Formik initialValues={{ title: "", desc: "", gallery: "" }}>
            {({ values, handleChange, setValues, handleSubmit }) => (
              <form className={s.body} onSubmit={handleSubmit}>
                <Input
                  label="Назва"
                  value={values.title}
                  name="title"
                  onChange={handleChange}
                  containerClass={s.input__container}
                />
                <div className={s.input__container}>
                  <p className={s.label}>Фото</p>
                  <div className={s.images__container}>
                    {values.gallery && (
                      <div className={s.image__container}>
                        <FontAwesomeIcon
                          icon={faTimes}
                          className={s.delete__icon}
                          onClick={() => {
                            uploadInputRef.current.value = null;
                            setValues({ ...values, gallery: "" });
                          }}
                        />
                        <img
                          className={s.image}
                          src={values.gallery}
                          alt="loading"
                        />
                      </div>
                    )}
                  </div>
                  <input
                    type="file"
                    ref={uploadInputRef}
                    onChange={(e) => handleImages(e, values, setValues)}
                  />
                </div>
                <Input
                  label="Опис"
                  value={values.desc}
                  name="desc"
                  onChange={handleChange}
                  containerClass={s.input__container}
                  isTextarea
                />
                <div className={s.submit__container}>
                  <Button title="Створити" type="submit" size="lg" />
                </div>
              </form>
            )}
          </Formik>
        </FixedWrapper>
      </div>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {};
};
const mapDispatchToProps = (dispatch) => {
  return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(CreateNews);
